*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

ul {
  list-style: none;
}

img {
  height: auto;
  max-width: 100%;
}

button {
  cursor: pointer;
}

input {
  background-color: transparent;
}

a {
    text-decoration: none;
  }
.container {
    width: 100%;
    margin: 0 auto;
    padding: 0 40px;
    max-width: 1440px;
  }
  